	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wine School 2","date":"23 Jan 2007","slug":"/2007-01-23-wine-school-2/","tags":"Y2007"},"html":"<p>I resumed the <a href=\"http://www.stern.de/weinschule\">Stern wine school</a>, which I <a href=\"http://www.papascott.de/archives/2007/01/16/wine-school/\">started last week</a>, on the night of the big storm last Thursday. The next lesson was another Bordeaux, this one a price class higher. The <a href=\"http://www.stern.de/lifestyle/kueche/getraenke/580331.html?eid=580464\">2004 Dutruch</a> costs 20, so I wasn't greatly disappointed that my first impression was that it was way too bitter for my taste. I certainly did not detect the aroma of tea leaves, tobacco or pencil shavings mentioned by Messrs. Lange and Lange.</p>\n<p>Unforunately for my wallet the Dutruch tasted much better the next day, after I had left the remaining half-bottle corked at room temperature overnight. I guess I should take the temperature recommendations and the suggestion that it be decanted more seriously. The price is too rich for for me for everyday consumption, but I could see keep a few bottles in the basement (or in the closet, as the Langes suggest) for special occasions. It's supposed to only get better for the next 3 to 5 years.</p>\n<p>The next wine is also from Bordeaux, this time a fresh white wine. It doesn't seem right for January, but we'll see.</p>"},"next":{"fields":{"title":"Coming Home","date":"23 Jan 2007","slug":"/2007-01-23-coming-home/"}},"previous":{"fields":{"title":"Linux Can Be Dangerous","date":"25 Jan 2007","slug":"/2007-01-25-linux-can-be-dangerous/"}}},{"node":{"fields":{"title":"Coming Home","date":"23 Jan 2007","slug":"/2007-01-23-coming-home/","tags":"Y2007"},"html":"<p>The <a href=\"http://www.radiohamburg.de/index.html?webseiteID=1550&amp;webcontainerID=9094\">Bundestagkantine</a> radio show this morning (on the investigation of whether the Schröder government hindered the release of Murat Kurnaz from Guantanamo) wasn't particularly funny, although the comment that \"with that beard we thought he was <a href=\"http://www.bundestag.de/mdb/bio/T/thierwo0.html\">Wolfgang Thierse</a>\" was mildly amusing.</p>\n<p>However, it was very funny that the next song on Radio Hamburg was <a href=\"http://www.youtube.com/watch?v=eKVAV3qSb8U\">\"Coming Home\" by Sasha</a>.</p>"},"next":{"fields":{"title":"City of Books and Pillows","date":"21 Jan 2007","slug":"/2007-01-21-city-of-books-and-pillows/"}},"previous":{"fields":{"title":"Wine School 2","date":"23 Jan 2007","slug":"/2007-01-23-wine-school-2/"}}},{"node":{"fields":{"title":"City of Books and Pillows","date":"21 Jan 2007","slug":"/2007-01-21-city-of-books-and-pillows/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/364544969/\" title=\"Photo Sharing\"><img src=\"http://farm1.static.flickr.com/162/364544969_5f67836ec9.jpg\" width=\"500\" height=\"375\" alt=\"IMG 1895\" /></a></p>\n<p>Christopher and his city of books and pillows. If you've seen &quot;Lilo and Stich&quot;, you'll know what he has planned (key word: destruction).</p>"},"next":{"fields":{"title":"Billary Returns","date":"21 Jan 2007","slug":"/2007-01-21-billary-returns/"}},"previous":{"fields":{"title":"Coming Home","date":"23 Jan 2007","slug":"/2007-01-23-coming-home/"}}}],"pathPrefix":"","first":false,"last":false,"index":161,"pageCount":1101}}

